import React, { Component } from 'react';
import {
  Card, CardImg, CardBody, CardText, CardTitle, Breadcrumb,
  BreadcrumbItem, Button, Modal, ModalHeader, Label, ModalBody
} from 'reactstrap';
import moment from 'moment';
import { Link } from 'react-router-dom';
import { LocalForm, Control, Errors } from 'react-redux-form';
import { Loading } from './LoadingComponant';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

// Validation variables
const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);

class CommentForm extends Component {
  constructor(props) {
    super(props);


    this.state = {
      isModalOpen: false
    };
    this.toggleModal = this.toggleModal.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }


  toggleModal() {
    console.log(this.state.isModalOpen);
    this.setState({ isModalOpen: !this.state.isModalOpen });
  }

  handleSubmit(values) {
    console.log("Values " + values);
    console.log('Current State is: ' + JSON.stringify(values));
    alert('Current State is: ' + JSON.stringify(values));
    this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
  }

  renderForm() {
    return (
      <Modal isOpen={this.state.isModalOpen}>
        <ModalHeader toggle={this.toggleModal}>Submit Comment
         </ModalHeader>
        <ModalBody>
          <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
            <div className="form-group">
              <Label htmlFor="rating">Rating</Label>
              <Control.select model=".rating" id="rating" name="rating" className="form-control">
                <option defaultValue="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
              </Control.select>
            </div>
            <div className="form-group">
              <Label htmlFor="author">Author</Label>
              <Control.text model=".author" id="author" name="author" placeholder="Your Name" className="form-control"
                validators={{ required, minLength: minLength(3), maxLength: maxLength(15) }} />
              <Errors className="text-danger" model=".author" show="touched"
                messages={{
                  required: "Required ",
                  minLength: "Must be greater than 2 characters.",
                  maxLength: "Must be 15 characters or less."
                }} />
            </div>
            <div className="form-group">
              <Label htmlFor="comment">Comment</Label>
              <Control.textarea model=".comment" id="comment" name="comment" className="form-control" />
            </div>
            <Button type="submit" value="submit" color="primary">Submit</Button>
          </LocalForm>
        </ModalBody>
      </Modal>
    );
  }

  render() {
    //console.log("Rendering Form Component");
    return (
      <div>
        {this.renderForm()}
        <Button outline onClick={this.toggleModal}>
          <span className="fa fa-pencil fa-lg"></span> Submit Comment
          </Button>
      </div>
    );
  }
}

class DishDetail extends Component {

  renderDish(selectedDish) {
    return (
      <FadeTransform
        in
        transformProps={{
          exitTransform: 'scale(0.5) translateY(-50%)'
        }}>
        <Card>
          <CardImg width="100%" src={baseUrl + selectedDish.image} alt={selectedDish.name} />
          <CardBody>
            <CardTitle><b>{selectedDish.name}</b></CardTitle>
            <CardText>{selectedDish.description}</CardText>
          </CardBody>
        </Card>
      </FadeTransform>
    );
  }



  renderComments(comments, postComment, dishId) {
    const dishComments = comments.map((commentDetails) => {
      console.log("Inside Render Comments commentDetails: ", commentDetails);
      console.log("Inside Render Comments addComment : ", postComment);
      var authorDate = "-- " + commentDetails.author + " ";
      var commentDate = new Date(commentDetails.date);
      authorDate += moment(commentDate).format('ll');

      return (
        <Fade in>
          <li key={commentDetails.id}>
            <p>{commentDetails.comment}</p>
            <p>{authorDate}</p>
          </li>
        </Fade>
      );

    });
    return (
      <div>
        <h3><b>Comments</b></h3>
        <ul className="list-unstyled">
          <Stagger in>
            {dishComments}
          </Stagger>
        </ul>
        <CommentForm dishId={dishId} postComment={postComment} /> {/*Calling class component CommentForm for interting the insert comment modal */}
      </div>
    );


  }

  render() {

    console.log("Inside Dish Details ", this.props.dish);
    console.log("Comments: " + this.props.comments);
    // console.log("this.props.dish.id = ",this.props.dish.id);

    if (this.props.isLoading) {
      return (
        <div className="container">
          <div className="row">
            <Loading />
          </div>
        </div>
      );
    }
    else if (this.props.errMess) {
      return (
        <div className="container">
          <div className="row">
            <h4>{this.props.errMess}</h4>
          </div>
        </div>
      );
    }
    else if (this.props.dish != null) {
      return (
        <div className="container">
          <div className="row">
            <Breadcrumb>
              <BreadcrumbItem>
                <Link to='/menu'>Menu</Link>
              </BreadcrumbItem>
              <BreadcrumbItem active>{this.props.dish.name}</BreadcrumbItem>
            </Breadcrumb>
            <div className="col-12">
              <h3>{this.props.dish.name}</h3>
              <hr />
            </div>
          </div>
          <div className="row">
            <div className="col-12 col-md-5">{this.renderDish(this.props.dish)}</div>
            {/* This will return the comment section of dish */}
            <div className="col-12 col-md-5">{this.renderComments(this.props.comments, this.props.postComment, this.props.dish.id)}</div>
          </div>
        </div>
      );
    }
    else {
      return (
        <div></div>
      );
    }

  }

}

export default DishDetail; 


