import * as ActionTypes from './ActionTypes';
import { baseUrl } from '../shared/baseUrl';

console.log("Inside ActionCreator.js");
export const addComment = (comment) =>({
    type: ActionTypes.ADD_COMMENT,
    payload:comment
});

export const postComment =(dishId, rating,author,comment) => (dispatch) =>{
    const newComment = {
        dishId:dishId,
        rating:rating,
        author:author,
        comment:comment
    }
    newComment.date = new Date().toISOString();
    return fetch(baseUrl + 'comments',{
          method:'POST',
          body: JSON.stringify(newComment),
          headers:{
              'Content-Type':'application/json'
          },
          credentials:'same-origin'
    })
       .then(response => {
           if(response.ok){
                return response;
           }
           else{
               var error = new Error('Error'+ response.status + response.statusText);
               error.response = response;
               throw error;
           }
        },   
        error => {                                     //Error when no response,server related error like Failed to fetch
            var errmess = new Error(error.message);
                throw errmess;
        }
    )
    .then(response => response.json())
    .then(response => dispatch(addComment(response))) //Passing dishes to addDishes method
    .catch(error =>{console.log('Post Comments ',error.message);
                    alert("Your comment cannot be posted ",error.message)});
}

export const fetchDishes = () => (dispatch) => {
    dispatch(dishesLoading(true));
    //return fetch(baseUrl + 'dishees')  // return error 404 not found
    return fetch(baseUrl + 'dishes') 
       .then(response => {
           if(response.ok){
                return response;
           }
           else{
               var error = new Error('Error'+ response.status + response.statusText);
               error.response = response;
               throw error;
           }
        },   
        error => {                                     //Error when no response,server related error like Failed to fetch
            var errmess = new Error(error.message);
                throw errmess;
        }) 
    .then(response => response.json())
    .then(dishes => dispatch(addDishes(dishes))) //Passing dishes to addDishes method
    .catch(error =>{dispatch(dishesFailed(error.message))});
}



export const dishesLoading = () => ({
    type: ActionTypes.DISHES_LOADING
});


export const dishesFailed = (errmess) =>({
    type:ActionTypes.DISHES_FAILED,
    payload:errmess
});

export const addDishes = (dishes) =>({
    type: ActionTypes.ADD_DISHES,
    payload: dishes
});

export const fetchComments = () => (dispatch) => {
    return fetch(baseUrl + 'comments') // return object
       .then(response => response.json()) // will convert into json
       .then(comment => dispatch(addComment(comment))); //Passing dishes to addDishes method
}

