// Post FeedBack   
export const postFeedback =(firstname,lastname,telnum,email,agree,contactType,message) =>{
    const newFeedback = {
        firstname : firstname,
        lastname : lastname,
        telnum : telnum,
        email : email,
        agree : agree,
        contactType : contactType,
        message : message
    }
    newFeedback.date = new Date().toJSON();
    return fetch(baseUrl + 'feedback',{
          method:'POST',
          body: JSON.stringify(newFeedback),
          headers:{
              'Content-Type':'application/json'
          },
          credentials:'same-origin'
    })
       .then(response => {
           if(response.ok){
                return response;
           }
           else{
               var error = new Error('Error'+ response.status + response.statusText);
               error.response = response;
               throw error;
           }
        },   
        error => {                                     //Error when no response,server related error like Failed to fetch
            var errmess = new Error(error.message);
                throw errmess;
        }
    )
    .then(response => response.json())
    .then(response => alert('Thank you for the feedback!\n'+ JSON.stringify(response))) //Passing dishes to addDishes method
    .catch(error =>{console.log('Post Feedback ',error.message);
                    alert("Your Feedback cannot be posted ",error.message)});
}



